.personInfo{
    max-width: 265px;
    width: 265px;
    background-color: #008080;
    border-radius: 7px;
    color: snow;
    padding:15px;
    height:300px;
    display: inline-block;
}
.personInfo-Title{
    text-align: center;
    background-color: #006667;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    margin-top:-15px;
    margin-left:-15px;
    margin-right:-15px;
    padding-top:10px;
    padding-bottom:10px;
    font-size: larger;
}
.personInfo-Name{
    text-align: center;
    margin-top:10px;
    margin-bottom: 20px;
}

.personAverageInfo-Link{
    margin-top: 155px;
    text-decoration: underline;
    cursor: pointer;
    text-align: center;
    &:hover{
        color: white;
    }
}

.personInfo-Link{
    margin-top:60px;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    color: white;
    
}
.personInfo-Link > a{
    color: white;
    background: #cc0000;
    border-radius: 5px;
    color: white;
    padding: 5px 38px;
    &:hover{
        color: white;
        text-decoration: none;
    }
}
.lifespansSubTitle{
    display: block;
    font-size: large;
}

.lifespanLinkWrapper{
    margin-top: 125px;
    text-align: center;
    &:hover{
        color: white;
    }
    a{
        background: #cc0000;
        border-radius: 5px;
        color: white;
        padding: 5px 70px;

        &:hover{
            color: white;
            text-decoration: none;
        }
    }
}
.textLink{
    margin-top:60px;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    color: white;
}
.birthDate{
margin-left: 13px;
}

.deathDate{
margin-left: 4px;
}

.personInfo-Age{
    margin-left: 53px;
}