.personRows-Wrapper{
    width: 24vw;
    text-align: center;
    margin-left: 38vw;
    margin-top: 10vw;
    background-color: #008080;
    border-radius: 7px;
    color: snow;
    padding:15px;
    height:290px;
}

.personRows-Title{
    text-align: center;
    background-color: #006667;
    border-top-right-radius: 7px;
    border-top-left-radius: 7px;
    margin-top:-15px;
    margin-left:-15px;
    margin-right:-15px;
    padding-top:10px;
    padding-bottom:10px;
}
.row{
    margin-left: 0px;
    color: snow;
    &:hover{
        color: snow;
        text-decoration: none;
    }
    background: #800000;
    border-radius: 5px;
    width: 100%;
    margin-bottom: 2px;
    display: block;
    padding-left: 10px;
    text-align: center;
    padding-top: 3px;
    padding-bottom: 3px;
}
.boldedText{
    font-weight: bold;
}
.personRows-Row{
    height: 230px;
    overflow-y: scroll;
    overflow-x: hidden;
    padding-top: 3px;
    display: flex;
    flex-direction: column;
}
.instructions{
    text-align: center;

}