.App-header {
    height: 62px;
    background: #006667;
  }
  
  .title{
    float:left;
    margin:16px;
    font-size: 15pt;
    font-weight: bold;
    color: #ffffff !important;
    font-family: 'Frank Ruhl Libre', serif;
    &:hover{
      text-decoration: none;
      color: white !important;
    }
    .subTitle{
      font-size: 11pt;
      font-weight: norma;
    }
    a{
      color: white;
      &:hover{
        text-decoration: none;
        color: white;
      }
    }
  }

  .buttonsWrapper{
    float: right;
  }

  .logout{
    margin:16px;
    display: inline-block;
  }

  .homeButton{
    display: inline-block;
  }

  .button{
    color: black;
    border: 1px solid #afafaf;
    padding:6px;
    padding-top: 4px;
    background-color: #e6e6e6;
    text-decoration: none;
    margin:13px;
    display: inline-block;
  }
  