.home{
    display: inline-flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
}

.homeTitle{
    font-size: xx-large;
    text-align: center;
    font-weight: bolder;
    margin-top: 7vh;
}

.menuWrapper{
    display: inline-flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.mainButton{
    width: 275px;
    height: 50px;
    border: 1px solid lightgray;
    line-height: 50px;
    border-radius: 5px;
    color: snow;
    background: #cc0000;
    display: block;
    text-align: center;
    margin-bottom: 15px;
    font-size: larger;
    &:hover{
        color: white;
    }
}
.homeBox{
    span{
        font-family: archivo;
    }
}

.pageHeader{
    font-size: -webkit-xxx-large;
    text-align: center;
    font-weight: bolder;
    margin-top: 20px;
}