.calendarWrapper{
    width: 60vw;
    margin-left: 20vw;
    margin-top: 0%;
}
.calendarRow{
    background: #006668;
    display: flex;
    flex-direction: row;
}

.number{
    position: relative;
    right: 9px;
    color: snow;
    top: 10px;
    font-size: large;
}

.calendarRow:not(last-child){
    justify-content: space-evenly;
}
.calendarRow:last-child{
    justify-content: left;
    .dateBox{
        margin-left: 37px;
    }
}

.dateBox{
    .date{
        top: -13px;
        left: -13px;
        position: relative;
        font-size: small;
        color: white;
    }
    background: #008080;
    margin: 15px;
    width: 70px;
    height: 70px;
    text-align: center;
    line-height: 50px;
    border-radius: 5px;
    cursor: pointer;
}

.monthTitle{
    text-align: center;
    font-weight: bolder;
    font-size: 19px;
}