.tab{
    width: 115px;
    margin-right:15px;
    margin-left:15px;
    border: 1px solid #066678;
    background-color: #008080;
    padding: 5px;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    text-align: center;
    font-family: archivo;
    font-size: large;
    cursor: pointer;
    color: white;
    &:hover{
        color: white;
        text-decoration: none;
    }
}

.selectedTab{
    background-color: #3da1a1;
}

