.lifeSpans-Wrapper{
    display: flex;
    flex-direction: column;
    margin-top: 4vh;
    margin-bottom: 13vh;
}
.wrapper-last{
    margin-bottom: 13vh;
}

.lifespans-header{
    font-size: -webkit-xxx-large;
    text-align: center;
    font-weight: bolder;
    margin-top: 20px;
}

.lifespans-info{
    display: block;
    text-align: center;
    font-weight: bold;
    font-size: xx-large;
    margin-bottom: 25px;
}

@keyframes fade-in {
    from {opacity: 0; transform: scale(.7,.7)}
    to {opacity: 1;}
}
.fade-in-element {
  animation: fade-in 1.4s;
}

.hidden {
    opacity: 0;
  }
  
  .innerwrapper{
      display: flex;
      justify-content: space-evenly;
  }

  .selectGroup{
      background: #008080;
      color: white;
      display: inline-block;
      margin: 15px;
      padding: 5px 10px;
      border-radius: 5px;
      &:hover{
          color: white;
          text-decoration: none;
      }
      a{
          color: white;
          &:hover{
              color: white;
              text-decoration: none;
          }
      }
      &.selected{
          background:#3da1a1;
          cursor: default;
      }
  }