.bargraphTitle{
  text-align: center;
  margin-top: 15px;
  font-weight: bolder;
  font-size: 2.5rem;
}

.xAxis{
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;
}

.yAxis{
  //margin-top: 14%;
  float: left;
  margin-top: 10%;
  margin-left: 2%;
  font-weight: bold;
  font-size: 1.2rem;



  //left: -35px;
}

.bargraph {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: stretch;
    height: 350px;
    width: 70%;
    background: #ffe5e5;
    margin-left: 15%;
    margin-top: 2%;
  }
  
  .bar {
    margin: 5px;
    &[data-title] {
     margin-bottom: 1.7rem; 
    }
    /*Overridden by flexbox:*/
    width: 100%;
    background: #cc0000;
    position: relative;
    
    &::before {
      content: attr(data-val);
      top: 0;
      color: white;
      font-weight: bold;
    }
    &::after {
      content: attr(data-title);
      color: black;
      top: 100%;
    }
    &::before, &::after {
      //font-family: sans-serif;
      font-size: 1.2rem;
      text-align: center;
      position: absolute;
      left: 0;
      right: 0;
    }
    cursor: pointer;
  
    &.flip-percentage::before {
      top: unset;
      bottom: 100%;
      color: black;
    }
  }
  
  /* ========= Extra stuff ========= */
  
  button {
    border-radius: 5px;
    border: 1px solid #8a9da9;
    background: none;
    outline: none;
    color: #8a9da9;
    &:active {
      background: lightgray;
    }
  }
  .barInstructions{
    text-align: center;
    font-weight: bold;
    margin-top:5px;
  }
  .graphDescription{
    text-align: center;
    color: gray;
    margin-bottom: 15px;
    font-size: large;
  }
  
  // Copied from JSFiddle styling
  * {
    font-family: Inconsolata,Monaco,"Andale Mono","Lucida Console","Bitstream Vera Sans Mono","Courier New",Courier,monospace;
  }